import React, { createContext, useContext, useState, useEffect } from 'react';
import axios from 'axios';

// API URL'yi en bata tanmlayalm
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

// Context'i oluturalm
const AuthContext = createContext(null);

// Hook'u tanmlayalm
export function useAuth() {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}

export function AuthProvider({ children }) {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Token kontrol
  const checkAuth = async () => {
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        setLoading(false);
        return;
      }

      console.log('Token kontrol ediliyor...');
      const response = await axios.get(`${API_URL}/api/auth/me`, {
        headers: { Authorization: `Bearer ${token}` },
        withCredentials: true
      });
      
      if (response.data) {
        console.log('Kullanc bilgileri alnd:', response.data);
        setUser(response.data);
      } else {
        console.log('Kullanc bulunamad, token siliniyor');
        localStorage.removeItem('token');
        setUser(null);
      }
    } catch (err) {
      console.error('Token kontrol hatas:', err);
      localStorage.removeItem('token');
      setUser(null);
      setError(err.response?.data?.message || 'Kimlik dorulama hatas');
    } finally {
      setLoading(false);
    }
  };

  // lk yklemede token kontrol
  useEffect(() => {
    checkAuth();
  }, []);

  // Callback ilemi
  const handleCallback = async (token) => {
    try {
      setLoading(true);
      setError(null);

      if (!token) {
        throw new Error('Token bulunamad');
      }

      console.log('Token kaydediliyor...');
      localStorage.setItem('token', token);
      
      console.log('Kullanc bilgileri alnyor...');
      await checkAuth();
      
      return true;
    } catch (err) {
      console.error('Callback hatas:', err);
      setError(err.response?.data?.message || 'Kimlik dorulama baarsz oldu. Ltfen tekrar deneyin.');
      localStorage.removeItem('token');
      setUser(null);
      return false;
    } finally {
      setLoading(false);
    }
  };

  // k ilemi
  const logout = () => {
    localStorage.removeItem('token');
    setUser(null);
    setError(null);
  };

  // Context deerini oluturalm
  const value = {
    user,
    loading,
    error,
    isAuthenticated: !!user,
    handleCallback,
    logout
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
} 